import React from "react";
import PatientListForm from "./Components/PatientListForm";
import { Container, Stack } from "@mui/material";
import useStateMachine from "./StateMachine/useStateMachine";
import Patients from "./Components/Patients";
import { useContext } from "react";
import { GlobalContext } from "Global/Context/GlobalContextProvider";

const PatientList = () => {
  const languageArray = useContext(GlobalContext)?.LanguageArray;

  const [StateBank, setFunctionArray] = useStateMachine();

  return (
    <React.Fragment>
      <Container maxWidth="md">
        <Stack marginTop="1em" alignItems="center" gap={".5em"}>
          <PatientListForm
            StateBank={StateBank}
            setFunctionArray={setFunctionArray}
            languageArray={languageArray}
          />
          <Patients
            patientList={StateBank?.PatientList}
            languageArray={languageArray}
          />
        </Stack>
      </Container>
    </React.Fragment>
  );
};

export default PatientList;
