import React from "react";
import useGatherDataFromApi from "Global/Hooks/useGatherDataFromApi";
import { GetInsuranceListPublic } from "Global/Adapters/GetInsuranceListPublic";
import { GetDetectionList } from "../Adapters/GetDetectionList";
import { GetPatientList } from "../Adapters/GetPatientList";
import { DefaultData } from "../Variables/DefaultData";

const useStateMachine = () => {
  const [FunctionArray, setFunctionArray] = React.useState([]);
  const [StateBank, setStateBank] = React.useState();
  React.useLayoutEffect(() => {
    setFunctionArray([
      {
        functions: GetInsuranceListPublic,
        parameters: "InsuranceList",
      },
      {
        functions: GetDetectionList,
        parameters: "DetectionList",
      },
      // {
      //   functions: GetPatientList,
      //   parameters: {
      //     objName: "PatientList",
      //     data: DefaultData,
      //   },
      // },
    ]);
  }, []);
  const { gatheredData } = useGatherDataFromApi(FunctionArray);

  React.useMemo(() => {
    setStateBank(gatheredData);
  }, [gatheredData, FunctionArray]);
  return [StateBank, setFunctionArray];
};

export default useStateMachine;
