import React from "react";
import SearchTypeSelector from "./SearchTypeSelector";
import CustomizableSelect from "Global/Components/CommonComponents/CustomizableSelect/CustomizableSelect";
import DateRangePicker from "Global/Components/CommonComponents/DateRangePicker/DateRangePicker";
import PersianSupportTextField from "Global/Components/UnEditableComponents/PersianSupportTextField/PersianSupportTextField";
import { Search } from "@mui/icons-material";
import { DefaultData } from "../Variables/DefaultData";
import usePatientFormLogicPack from "../Hooks/usePatientFormLogicPack";
import { useTranslation } from "react-i18next";
import FindLangKey from "Global/Functions/FindLangKey/FindLangKey";

const PatientListForm = ({ StateBank, setFunctionArray, languageArray }) => {
  const { t, i18n } = useTranslation();

  const {
    setSearchType,
    setInsuranceSelector,
    setDetectorList,
    setDateRangeValue,
    HandlePatientSearchField,
  } = usePatientFormLogicPack(setFunctionArray);

  const searchBaseMsg = FindLangKey("general-public-search-based-on");

  return (
    <React.Fragment>
      <SearchTypeSelector
        setSearchType={setSearchType}
        languageArray={languageArray}
      />
      {/* <p style={{ textAlign: "center", fontSize: ".8em" }}>{searchBaseMsg}</p> */}

      <CustomizableSelect
        label={FindLangKey("general-public-insurance-list", languageArray)}
        SelectableDataArray={StateBank?.InsuranceList}
        GetSelectedValue={setInsuranceSelector}
      />
      <CustomizableSelect
        label={FindLangKey("patient-list-select-diagnosis-list", languageArray)}
        SelectableDataArray={StateBank?.DetectionList}
        GetSelectedValue={setDetectorList}
      />
      <DateRangePicker
        GetDateRange={setDateRangeValue}
        ExcludeOneMonth={true}
        KeepSelectedDate={true}
      />
      <PersianSupportTextField
        label={FindLangKey("patient-list-input-search-name-id", languageArray)}
        onlyIcon={<Search />}
        CustomOnChange={HandlePatientSearchField}
        isEnglish={i18n?.language === "en"}
        styles={{
          marginTop: "-1em",
        }}
      />
    </React.Fragment>
  );
};

export default PatientListForm;
