import React, { useContext } from "react";
import { GlobalContext } from "Global/Context/GlobalContextProvider";
import EachPatient from "Routes/Protected/CurrentPatients/Components/EachPatient/EachPatient";
import { Card } from "@mui/material";
import styles from "Routes/Protected/CurrentPatients/Styles/Current.Patient.module.scss";
import useOptimizeListRenderPerformance from "Global/Hooks/useOptimizeListRenderPerformance";
import useSortSelectedPatientsToTop from "Global/Hooks/useSortSelectedPatientsToTop";
const Patients = ({ patientList }) => {
  const userFilePatients = useSortSelectedPatientsToTop(patientList);
  const { list, handleTouchMove, handleScroll } =
    useOptimizeListRenderPerformance(userFilePatients);
  const GlobalConstants = useContext(GlobalContext);

  return (
    <React.Fragment>
      <div
        style={{
          all: "inherit",
          display: "flex",
          flexDirection: "column",
          gap: "8px",
          width: "100%",
        }}
        onWheel={handleScroll}
        onTouchMove={handleTouchMove}
      >
        {list?.map((Patient, index) => (
          <Card
            elevation={2}
            sx={{
              p: ".3em",
              borderRadius: "20px",
              width: "100%",
            }}
            className={styles.PatientCard}
            key={index}
          >
            <EachPatient
              Patient={Patient}
              key={GlobalConstants.uuidV4()}
              bgColor={
                Patient?.EndOfTreatment
                  ? "yellowgreen"
                  : Patient?.CancelByDoctor
                  ? "yellow"
                  : ""
              }
              disableActions={
                Patient?.EndOfTreatment || Patient?.CancelByDoctor
              }
            />
          </Card>
        ))}
      </div>
    </React.Fragment>
  );
};

export default Patients;
