import React from 'react';
import { Stack } from '@mui/material';
import { useTranslation } from 'react-i18next';
import FindLangKey from 'Global/Functions/FindLangKey/FindLangKey';
import EditableCustomizableSelect from 'Global/Components/EditableComponents/EditableCustomizableSelect/EditableCustomizableSelect';

const SearchTypeSelector = ({
  setSearchType,
  languageArray,
}) => {
  const { t, i18n } = useTranslation();

  const type_array = [
    {
      ItemContent: FindLangKey(
        'patient-list-radio-type-all',
        languageArray
      ),
      ItemValue: '0',
    },
    {
      ItemContent: FindLangKey(
        'patient-list-radio-type-ended',
        languageArray
      ),
      ItemValue: '2',
    },
    {
      ItemContent: FindLangKey(
        'patient-list-radio-type-canceled',
        languageArray
      ),
      ItemValue: '3',
    },
  ];

  const type_label = FindLangKey(
    'lab-patient-tarikh-type-label'
  );

  return (
    <React.Fragment>
      <Stack width="100%">
        <EditableCustomizableSelect
          SelectableDataArray={type_array}
          initialData={[
            {
              ItemContent: FindLangKey(
                'patient-list-radio-type-all',
                languageArray
              ),
              ItemValue: FindLangKey(
                'patient-list-radio-type-all',
                languageArray
              ),
            },
          ]}
          label={type_label}
          onChange={(val) => {
            setSearchType(val);
          }}
          enableInitialOnchange
          isSingle
        />
      </Stack>
    </React.Fragment>
  );
};

export default SearchTypeSelector;
