import React from "react";
import { GetPatientList } from "../Adapters/GetPatientList";
import { DefaultData } from "../Variables/DefaultData";
import { GetInsuranceListPublic } from "Global/Adapters/GetInsuranceListPublic";
const usePatientFormLogicPack = (setFunctionArray) => {
  const [InsuranceSelector, setInsuranceSelector] = React.useState();
  const [SearchType, setSearchType] = React.useState("");
  const [PatientSearchField, setPatientSearchField] = React.useState("");
  const [DateRangeValue, setDateRangeValue] = React.useState("");
  const [DetectorList, setDetectorList] = React.useState("");
  const HandlePatientSearchField = (event) => {
    setPatientSearchField(event.target.value);
  };

  React.useEffect(() => {
    if (
      InsuranceSelector?.ItemContent &&
      !Object.values(DateRangeValue).some((item) => item.length === 0)
    ) {
      setFunctionArray([
        {
          functions: GetInsuranceListPublic,
          parameters: "InsuranceList",
        },
        {
          functions: GetPatientList,
          parameters: {
            objName: "PatientList",
            data: {
              ...DefaultData,
              idInsuranceList: InsuranceSelector?.ItemContent?.join(","),
              idTashkhisList: DetectorList?.ItemContent.join(",") || "",
              patientSearchField:
                (PatientSearchField.length && PatientSearchField) || "",
              searchType: (SearchType.length && parseInt(SearchType)) || 0,
              fromDate: DateRangeValue?.FromDate,
              toDate: DateRangeValue?.ToDate,
            },
          },
        },
      ]);
    }
  }, [
    InsuranceSelector,
    SearchType,
    PatientSearchField,
    DateRangeValue,
    DetectorList,
  ]);
  return {
    setSearchType,
    setInsuranceSelector,
    setDetectorList,
    setDateRangeValue,
    HandlePatientSearchField,
  };
};

export default usePatientFormLogicPack;
