import SwalAlert from 'Global/Components/CommonComponents/SwalAlert/SwalAlert';
import Cookies from 'js-cookie';
import { Post } from 'Xhr/Xhr';
import { env } from 'env';

export const GetPatientList = async (
  setPatientData,
  parameters
) => {
  const Id = JSON.parse(Cookies.get('user'));

  console.log('parameters->', parameters);

  Post(
    'POST',
    `${process.env.REACT_APP_API_URL_API_DOCTORWORKLIST_PATIENT_LIST}`,
    true,
    {
      ...parameters.data,
      idPerson: Id.IdPerson,
    }
  ).then((response) => {
    if (response.data.tasks.resCode === 1)
      setPatientData &&
        setPatientData((prev) => ({
          ...prev,
          [parameters.objName]: response.data.tasks.Data,
        }));
    else
      SwalAlert(
        'error',
        response.data.tasks.resMessage,
        false
      );
  });
};
